.id-counter {
    & > * {
        float: left;
        display: inline-block;
        font-size: 16px;
        font-family: Arial;
        vertical-align: middle;
        box-sizing: border-box;
        padding: 3px 5px;
        border: 1px solid #A9A9A9;
        text-align: center;
        & + * {
            border-left: none;
            outline: none; } }

    &:after {
        content: "";
        display: block;
        clear: both; }

    &-increment, &-decrement {
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        padding-left: 10px;
        padding-right: 10px; }

    &-input {
        width: 50px; } }
